import React, { Component } from "react";
import { Link } from "react-router-dom";

class Nav extends Component {
  render() {
    return (
      <div>
        <label>Nombre</label>
        <input name="nombre" type="text" placeholder="Nombre..." />
        <button type="submit">Buscar</button>
        <Link to="/">Home</Link>
      </div>
    );
  }
}

export default Nav;
